<template>
  <div class="login-container">
    <el-form class="login-form" ref="loginForm" :model="loginForm" :rules="loginRules" autocomplete="on">
      <div class="title-container">
        <h3 class="title">登录</h3>
      </div>
      <el-form-item prop="username">
        <el-input
          ref="username"
          v-model="loginForm.username"
          placeholder="账户名"
          type="text"
        />
      </el-form-item>
      <el-form-item prop="password">
        <el-input
          ref="password"
          v-model="loginForm.password"
          placeholder="密码"
          name="password"
          type="password"
          :show-password="showPassword"
        />
      </el-form-item>

      <el-button class="login-btn" :loading="loading" type="primary" @click.native.prevent="handleLogin">登录</el-button>
    </el-form>
  </div>
</template>

<script>
  import {axioPost} from "../utils/httpUtil";
  import api from "../utils/api"
  export default {
    name: "login",
    data(){
      const validateUsername = (rule, value, callback) => {
        if (!value.toString().trim()) {
          callback(new Error('请输入账户名'))
        } else {
          callback()
        }
      };
      const validatePassword = (rule, value, callback) => {
        if (value.toString().trim().length < 6) {
          callback(new Error('密码长度为6-12位'));
        } else {
          callback()
        }
      };
      return{
        loginForm: {
          username: '',
          password: ''
        },
        loginRules: {
          username: [{ required: true, trigger: 'blur', validator: validateUsername }],
          password: [{ required: true, trigger: 'blur', validator: validatePassword }]
        },
        loading:false,
        showPassword:true,
      }
    },
    mounted(){
      if (this.loginForm.username === '') {
        this.$refs.username.focus()
      } else if (this.loginForm.password === '') {
        this.$refs.password.focus()
      }
    },
    methods:{
      async handleLogin(){
        this.$refs.loginForm.validate(async (valid) => {
          if (valid) {
            this.loading = true;
            try {
              let param = {
                username:this.loginForm.username.toString().trim(),
                password:this.loginForm.password.toString().trim()
              };
              let result = await axioPost(api.loginUrl,param);
              this.loading = false;
              console.log(result);
              if (result.code==200){
                this.$router.push({
                  path:"/main"
                });
              }else{
                this.$message.error(result.data.msg);
              }
            }catch (e) {
              this.loading = false;
              console.log("请求失败");
              this.$message.error('请求失败');
            }
          }
        });

      }
    }
  }
</script>

<style scoped lang="scss">

  $bg: #2d3a4b;
  $light_gray: #eee;

  .login-container {
    margin: 0;
    padding: 0;
    height: 100vh;
    width: 100%;
    background-color: $bg;
    overflow: hidden;
  }

  .login-form {
    width: 480px;
    margin: 0 auto;
    padding: 160px 0;
  }

  .title-container {
    position: relative;

    .title {
      font-size: 26px;
      color: $light_gray;
      margin: 0px auto 40px auto;
      text-align: center;
      font-weight: bold;
    }
  }

  .login-btn{
    width: 100%;
    margin-bottom: 30px;
  }
</style>



