import fetch from './fetch.js';



//post
export function axioPost (url,data) {
  return fetch({
    url: url,
    method: 'post',
    data: data
  });
}

//get
export function axioGet (url,data) {
  return fetch({
    url: url,
    method: 'get',
    params: data
  });
}



