import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import login from "@/views/login.vue"
import main from "@/views/main.vue"

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/helloworld',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path:"/",
      name:"login",
      component:login
    },
    {
      path:"/main",
      name:"main",
      component:main
    }
  ]
})



