import axios from 'axios';
import qs from 'qs';

let _service = axios.create({
  // 已经是一个promise 了
  // baseURL:'http://localhost:3001/admin',//测试
  baseURL:'http://8.129.184.96:3001/admin',//正式
  timeout: 15000,
});
// POST传参序列化(添加请求拦截器)
_service.interceptors.request.use(
  config => {
    // 在发送请求之前做某件事
    if (
      config.method === 'get' || 'post' || 'put' || 'delete'
    ) {
      // 序列化
      config.data = qs.stringify(config.data);
    }

    // 若是有做鉴权token , 就给头部带上token
    if (localStorage.token) {
      config.headers.Authorization = localStorage.token;

    }
    return config;
  },
  error => {
    return Promise.reject(error.data.error.message);
  }
);

_service.interceptors.response.use(function (response) {
  return response.data;
}, function (error) {
  return Promise.reject(error);
});

export default _service;



